<template>
  <div class="login-container">{{ $store.state.authToken }}
    <van-form ref="form" @submit="login" :rules="rules">
      <span class="title">AVE后台管理系统</span>
      <van-cell-group inset>
        <van-field
          v-model.trim="form.user_name"
          name="用户名"
          label="用户名"
          autocomplete="on"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model.trim="form.password"
          type="password"
          name="密码"
          label="密码"
          autocomplete="on"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
      </van-cell-group>
      <div style="margin: 16px">
        <van-button round size="small" block type="primary" native-type="submit">登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { login } from '@/api'
import store from '@/store'

export default {
  name: 'Login',
  beforeRouteEnter(to, from, next) {
    if (store.state.authToken) {
      next('/')
    } else {
      next()
    }
  },
  data() {
    return {
      form: {
        user_name: localStorage?.user_name || '',
        password: localStorage?.password || ''
      },
      rules: {
        user_name: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      },
      loading: false
    }
  },
  methods: {
    login() {
      this.loading = true
      login(this.form)
        .then(res => {
          if (res) {
            localStorage.user_name = this.form.user_name
            localStorage.password = this.form.password
            this.$notify({ type: 'success', message: '登录成功' })
            this.$store.commit('setToken', res?.token)
            this.$store.commit('setUserInfo', { user_name: res.user_name, role: res.role || 1 })
            if (res?.token) {
              if (res.role === 3) {
                this.$router.push('/check')
              } else if (res.role === -1) {
                this.$router.push('/token/logo')
              } else {
                this.$router.push('/')
              }
            }
          }
        })
        .catch(err => {
          console.log('----login--err----', err)
          this.$toast(err.msg || err.message || err || '登录失败')
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>

